<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item>
          <el-input placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-select v-model="fileType" placeholder="">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"></el-button>
        </el-form-item>
        <el-form-item>
          <el-upload ref="upload" :with-credentials="true" action="http://192.168.100.100:8080/lovdmx_control/uploadFile/uploadVideo" :data="videoParams" name="videoFile" :auto-upload="false" :limit="1" :on-change="chooseFile" :on-success="uploadSuccess" :on-error="uploadFail">
            <el-button type="primary"><i class="el-icon-upload el-icon--right"></i></el-button>
          </el-upload>
        </el-form-item>
      </el-form>
    </el-col>
    <!---->
    <el-table :data="showList" highlight-current-row style="width: 100%;">
      <el-table-column type="index"></el-table-column>
      <el-table-column label="" prop="filePath">
        <template slot-scope="scope">{{scope.row.filePath | fileName}}</template>
      </el-table-column>
      <el-table-column label="" prop="time">
        <template slot-scope="scope">{{scope.row.time | timeFormat}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag type="warning" v-if="scope.row.status===0"></el-tag>
          <el-tag type="danger" v-if="scope.row.status===1"></el-tag>
          <el-tag type="success" v-if="scope.row.status===2"></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" :disabled="scope.row.status!==0" @click="uploadFile(scope.row)"></el-button>
          <el-button size="mini" type="danger" :disabled="scope.row.status!==1" @click="sendVideoSplit(scope.row)"></el-button>
          <el-button size="mini" type="success" :disabled="scope.row.status!==2" @click="handleAdd(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" :page-size="10" :total="fileType==='light'?lightList.length:videoList.length" style="float:right;">
      </el-pagination>
    </el-col>
    <!--  -->
    <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false" width="500px">
      <el-form :model="addForm">
        <el-form-item label="">
          <el-input v-model="addForm.subtaskName" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false"> </el-button>
        <el-button type="primary" @click="addSubmit"> </el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
import Spirit from '@/api/spirit.js'
import { addSubtask, sendVideoSplit, sendEdlmxSplit } from '@/api/api.js'

var spirit = new Spirit('00:CF:E0:3C:52:37')
export default {
  data() {
    return {
      page: 0,
      videoParams: { //
        minute: '' //
      },
      fileType: 'light', // 
      options: [{
        value: 'light',
        label: ''
      }, {
        value: 'video',
        label: ''
      }],
      addFormVisible: false, //
      addForm: { //
        subtaskName: '', //
        filePath: '', //
        time: '', //
        md5: '', //md5
        type: '' //:0--1--
      }
    }
  },
  computed: {
    fileList: function() {
      return this.$store.state.fileList;
    },
    lightList: function() {
      var lightList = [];
      for (var i = 0; i < this.fileList.length; i++) {
        lightList = lightList.concat(this.fileList[i].lightFileList);
      }
      return lightList;
    },
    videoList: function() {
      var videoList = [];
      for (var i = 0; i < this.fileList.length; i++) {
        videoList = videoList.concat(this.fileList[i].videoFileList);
      }
      return videoList;
    },
    showList: function() {
      var showList = [];
      if (this.fileType === 'light') {
        showList = this.lightList.slice(this.page * 10, (this.page + 1) * 10);
      } else if (this.fileType === 'video') {
        showList = this.videoList.slice(this.page * 10, (this.page + 1) * 10);
      }
      return showList;
    }
  },
  components: {

  },
  methods: {
    //
    getFileList() {
      const that = this
      spirit.getFileLists()
        .then(res => {
          console.log(res)
          if (res.result === 'send successful') {
            var fileList = JSON.parse(res.directoryList);
            console.log(fileList)
            that.$store.commit('updateFileList', fileList);
          } else if (res.result === 'not online') {
            var fileList = [{
              lightFileList: res.uploadEdlmxList,
              videoFileList: res.uploadVideoList
            }];
            that.$store.commit('updateFileList', fileList);
          }
        })
        .catch(res => {
          that.$message.error('');
        })
    },

    //
    chooseFile(file) {
      if (file.status === 'ready') { //file.status==='ready'
        //video
        if (file.raw.type.indexOf('video') === -1) {
          this.$message.warning('');
          this.$refs.upload.clearFiles();
        } else {
          var video = file.raw;
          var url = URL.createObjectURL(video);
          var VideoObj = new Audio(url);
          const that = this;

          //
          VideoObj.onerror = function(res) {
            that.$message.warning('web');
            that.$refs.upload.clearFiles();
          }

          //fileList,
          VideoObj.oncanplaythrough = function() {
            var duration = parseInt(this.duration * 1000);
            that.videoParams.minute = duration;
            that.$refs.upload.submit();
          }
        }
      }
    },
    //
    uploadFail() {
      this.$message.error('');
      this.$refs.upload.clearFiles();
    },
    //
    uploadSuccess(res) {
      console.log(res)
      if (res === 'already exists') {
        this.$message.warning(',');
      } else if (res === 'succeed') {
        this.$message.success('');
      } else {
        this.$message.error('');
      }
      this.$refs.upload.clearFiles();
    },

    //
    uploadFile(data) {
      var fileType = data.fileType === 'DMX' ? 0 : 1;
      var path = data.filePath;
      var md5 = data.md5;
      var time = data.time;
      spirit.uploadFile(fileType, path, md5, time).then(res => { console.log(res) })
    },

    //
    changePages(val) {
      this.page = val - 1;
    },

    //
    handleAdd(data) {
      this.addFormVisible = true;
      this.addForm.filePath = data.filePath;
      this.addForm.time = parseInt(data.time);
      this.addForm.md5 = data.md5;
      this.addForm.type = data.fileType === 'DMX' ? 0 : 1;
    },

    //
    addSubmit() {
      const that = this;
      if (this.addForm.subtaskName === '') {
        this.$message.warning('');
      } else {
        addSubtask(this.addForm).then(res => {
          console.log(res)
          if (res.result === 'succeed') {
            that.addFormVisible = false;
            that.$message.success('');
          } else if (res.result === 'already exists') {
            that.$message.warning('');
          } else {
            that.$message.error('');
          }
        })
      }
    },

    //
    sendVideoSplit(data) {
      var params = {
        mac: null,
        logInfo: '',
        ids: data.uploadId
      }
      if (data.fileType === 'DMX') {
        sendEdlmxSplit(params).then(res => { console.log(res) })
      } else if (data.fileType === 'VIDEO') {
        sendVideoSplit(params).then(res => { console.log(res) })
      }
    }
  },
  created() {
    this.getFileList();
  }
}

</script>
<style></style>



