<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item>
          <el-input placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!---->
    <el-table :data="subtasksList" highlight-current-row style="width: 100%;">
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index"></el-table-column>
      <el-table-column label="" prop="subtaskName"></el-table-column>
      <el-table-column label="" prop="createTime">
        <template slot-scope="scope">{{scope.row.createTime | dateFormat}}</template>
      </el-table-column>
      <el-table-column label="" prop="filePath">
        <template slot-scope="scope">{{scope.row.filePath | fileName}}</template>
      </el-table-column>
      <el-table-column label="" prop="fileType">
        <template slot-scope="scope">{{scope.row.fileType | taskType}}</template>
      </el-table-column>
      <el-table-column label="" prop="time">
        <template slot-scope="scope">{{scope.row.time | timeFormat}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.row)"></el-button>
          <el-button size="mini" type="danger"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-button type="danger"></el-button>
      <el-pagination layout="prev, pager, next" :page-size="20" :total="subtasksList.length" style="float:right;"></el-pagination>
    </el-col>
    <!--  -->
    <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false" width="500px">
      <el-form :model="editForm">
        <el-form-item label="">
          <el-input v-model="editForm.subtaskName" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"> </el-button>
        <el-button type="primary" @click="editSubmit"> </el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
import { getPlayList, editSubtask } from '@/api/api.js'

export default {
  data() {
    return {
      editFormVisible: false,
      editForm: {
        subtaskName: '',
        subtaskId: ''
      }
    }
  },
  computed: {
    subtasksList: function() {
      return this.$store.state.subtasksList;
    }
  },
  methods: {
    //
    getSubtaskList() {
      const that = this;
      getPlayList().then(res => {
        console.log(res)
        that.$store.commit('updateSubtasksList', res.subtasksList);
      })
    },

    //
    handleEdit(data) {
      this.editFormVisible = true;
      this.editForm.subtaskName = data.subtaskName;
      this.editForm.subtaskId = data.subtaskId;
    },
    editSubmit() {
      const that = this;
      if (this.editForm.subtaskName !== '') {
        editSubtask(this.editForm).then(res => {
          console.log(res)
          if (res.result === 'succeed') {
            that.getSubtaskList();
            that.$message.success('!');
            that.editFormVisible = false;
          }else if(res.result==='already exists'){
            that.$message.warning('!');
          }
        })
      } else {
        this.$message.warning('');
      }
    }
  },
  created() {
    this.getSubtaskList();
  }
}

</script>
<style></style>



