<template>
  <section>
    <el-table :data="gatewayList">
      <el-table-column type="index"></el-table-column>
      <el-table-column align="center" label="" prop="rackName"></el-table-column>
      <el-table-column align="center" label="IP" prop="ip"></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-button type="success" circle v-if="scope.row.isOnline===1"></el-button>
          <el-button type="danger" circle v-if="scope.row.isOnline===0"></el-button>
        </template>
      </el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.mode" active-color="#13ce66" inactive-color="#ff4949" @change="changeMode"></el-switch>
        </template>
      </el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-button type="text" @click="$router.push({path:'/relay',query:{mac:scope.row.mac,rackId:scope.row.rackId}})"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-button type="primary" @click="$router.push({path:'/nodeList'})"></el-button>
      <el-button type="primary" @click="getGatewayInstantTask"></el-button>
    </el-col>
    <!--  -->
    <el-dialog title="" :visible.sync="instantTaskVisible" center>
      <el-row :gutter="40">
        <el-col style="padding-bottom:20px" :span="6" v-for="item in instantTaskList" :key="item.taskName">
          <el-button style="width:100%" type="primary" @click="quickActions(item.taskData)">{{item.taskName}}</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </section>
</template>
<script>
import { getGatewayList, getGatewayInstantTask, sendInstantTask, sendTimerTask } from '@/api/api.js'

export default {
  data() {
    return {
      gatewayList: [], //
      instantTaskVisible: false, //
      instantTaskList: [] //
    }
  },
  methods: {
    //
    getGatewayList() {
      const that = this;
      getGatewayList().then(res => {
        if (res.result === 'true') {
          console.log(res)
          that.gatewayList = res.intelligentGatewayList;
        }
      })
    },

    //
    getGatewayInstantTask() {
      const that = this;
      this.instantTaskVisible = true;
      getGatewayInstantTask().then(res => {
        if (res.result === 'true') {
          that.instantTaskList = res.realtimeTasksList
        }
      })
    },

    //
    quickActions(direction) {
      var params = JSON.parse(direction);
      this.sendInstantTask(params)
    },

    ///
    changeMode(){

    },

    //
    sendInstantTask(params) {
      const that = this;
      sendInstantTask(params).then(res => {
        if (res.result === 'send successful') {
          that.$message.success('');
        }
      })
    },

    //
    sendTimerTask(params) {
      sendTimerTask(params).then(res => { console.log(res) })
    },

    //
    openAutoMode(rackId) {
      var params = {
        rackId: rackId,
        taskMsg: {}
      }
      this.sendTimerTask(params);
    },

    //
    closeAutoMode(rackId) {
      var params = {
        rackId: rackId,
        taskMsg: []
      }
      this.sendInstantTask(params);
    }
  },
  created() {
    this.getGatewayList();
  }
}

</script>
<style></style>



