<template>
  <div class="echarts-pie"></div>
</template>
<script>
//Echarts
import echarts from 'echarts/lib/echarts';
//
import 'echarts/lib/chart/pie';
//
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import 'echarts/lib/component/legend';

export default {
  computed: {
    option: function() {
      var option = {
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        title: {
          text: '  ',
          left: 'left',
          textStyle: {
            fontSize: 14,
            fontWeight: 'normal'
          },
          padding: 13
        },
        legend: {
          orient: 'vertical',
          right: '2%',
          top: '5%',
          data: ['', '', '', '', ''],
        },
        series: [{
          name: '',
          type: 'pie',
          radius: ['35%', '70%'],
          center: ['35%', '50%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '20',
                fontWeight: 'bold',
                // color: '#fff'
              }
            }
          }
        }]
      }
      option.series[0].data = this.pieOptionData;
      return option;
    }
  },
  props: ['pieOptionData'],
  methods: {
    //
    drawPie() {
      this.echartsPie = echarts.init(document.getElementsByClassName('echarts-pie')[0])
      this.echartsPie.setOption(this.option)
    },
    echartsResize() {
      const that = this
      this.echartsPie.resize()
      window.addEventListener('resize', function() {
        that.echartsPie.resize()
      })
    }
  },
  mounted() {
    this.drawPie()
    this.echartsResize()
  },
  watch: {
    option: function() {
      this.drawPie()
      this.echartsResize()
    }
  }
}

</script>
<style></style>



