<template>
  <el-menu :default-active="currentRoute" class="left-navbar" background-color="#eef1f6" router>
    <el-menu-item index="/" route="/">
      <i class="el-icon-info"></i>
      <span slot="title"></span>
    </el-menu-item>
    <el-menu-item index="/device" route="/device">
      <i class="el-icon-info"></i>
      <span slot="title"></span>
    </el-menu-item>
    <el-menu-item index="/monitor" route="/monitor">
      <i class="el-icon-view"></i>
      <span slot="title"></span>
    </el-menu-item>
    <el-submenu index="/spirit">
      <template slot="title">
        <i class="el-icon-menu"></i>
        <span></span>
      </template>
      <el-menu-item index="/fileList" route="/fileList"></el-menu-item>
      <el-menu-item index="/playList" route="/playList"></el-menu-item>
      <el-menu-item index="/taskList" route="/taskList"></el-menu-item>
    </el-submenu>
    <el-menu-item index="ele" route="/ele">
      <i class="el-icon-news"></i>
      <span slot="title"></span>
    </el-menu-item>
    <el-menu-item index="/switch" route="/switch">
      <i class="el-icon-news"></i>
      <span slot="title"></span>
    </el-menu-item>
    <el-menu-item index="/warning" route="/warning">
      <i class="el-icon-bell"></i>
      <span slot="title"></span>
    </el-menu-item>
  </el-menu>
</template>
<script>
export default {
  data(){
    return {
      currentRoute:''
    }
  },
  created() {
    this.currentRoute = this.$router.currentRoute.fullPath
  }
}

</script>
<style>
ul.left-navbar {
  height: 100%;
}

</style>



