import Vue from 'vue'
import Router from 'vue-router'
import login from '@/pages/login'
import home from '@/pages/home'
import index from '@/pages/index'
import device from '@/pages/device'
import monitor from '@/pages/monitor'
import fileList from '@/pages/spirit/fileList'
import playList from '@/pages/spirit/playList'
import taskList from '@/pages/spirit/taskList'
import electricity from '@/pages/electricity'
import switchPage from '@/pages/switch'
import relay from '@/pages/relay'
import nodeList from '@/pages/nodeList'
import warning from '@/pages/warning'
import notFound from '@/pages/404'

Vue.use(Router)

export const router = new Router({
  routes: [{
    path: '/login',
    name: '',
    component: login
  }, {
    path: '/',
    name: '',
    component: home,
    leaf: true, //
    children: [{ path: '/', component: index, meta: { requireAuth: true } }]
  }, {
    path: '/',
    name: '',
    component: home,
    leaf: true, //
    children: [{ path: '/device', component: device, meta: { requireAuth: true } }]
  }, {
    path: '/',
    name: '',
    component: home,
    leaf: true, //
    children: [{ path: '/monitor', component: monitor, meta: { requireAuth: true } }]
  }, {
    path: '/',
    name: '',
    component: home,
    children: [
      { path: '/fileList', component: fileList, name: '', meta: { requireAuth: true } },
      { path: '/playList', component: playList, name: '', meta: { requireAuth: true } },
      { path: '/taskList', component: taskList, name: '', meta: { requireAuth: true } }
    ]
  }, {
    path: '/',
    name: 'warning',
    component: home,
    leaf: true, //
    children: [{ path: '/warning', component: warning, meta: { requireAuth: true } }]
  }, {
    path: '/',
    name: 'electricity',
    component: home,
    leaf: true, //
    children: [{ path: '/ele', component: electricity, meta: { requireAuth: true } }]
  }, {
    path: '/',
    name: '',
    component: home,
    leaf: true, //
    children: [{ path: '/switch', component: switchPage, meta: { requireAuth: true } }]
  }, {
    path: '/',
    name: '',
    component: home,
    leaf: true,
    children: [{ path: '/relay', component: relay, meta: { requireAuth: true } }]
  }, {
    path: '/',
    name: '',
    component: home,
    leaf: true, //
    children: [{ path: '/nodeList', component: nodeList, meta: { requireAuth: true } }]
  }, {
    path: '/404',
    name: '404',
    component: notFound
  }, {
    path: '*',
    redirect: "/404"
  }]
})
export default router;


//
router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) { // 
    /* 
      localStoragetokentoken
    */
    var token = JSON.parse(localStorage.getItem('token'))
    if (token && token.expires > new Date().getTime()) {
      next() // 
    } else {
      next({
        path: '/login',
        query: { redirect: to.fullPath }
        /*  */
      })
    }
  } else { // 
    next();
  }
})



