<template>
	<el-row>
		<el-col :span="24" class="currrent"></el-col>
		<el-col :span="24" class="voltage"></el-col>
		<el-col :span="24" class="temperature"></el-col>
	</el-row>
</template>

<script>
//Echarts
import echarts from 'echarts/lib/echarts'
//
import 'echarts/lib/chart/line'

//
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import "echarts/lib/component/legend"

export default {
  data() {
    return {
      option1: {
        title: {
          text: '  20180801',
          left: 'center',
          textStyle: {
            fontSize: 14,
            fontWeight: 'normal'
          },
          padding: 13
        },
        legend: {
          bottom: '2%',
          data: ['']
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c}'
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          data: ['0000', '0200', '0400', '0600', '0800', '1000', '1200', '1400', '1600', '1800', '2000', '2200']
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          scale: true
        },
        grid: {
          top: '13%',
          left: '3%',
          right: '4%',
          bottom: '15%',
          containLabel: true
        },
        series: [{
          name: '',
          type: 'line',
          data: [1.1, 1.0, 1.3, 1.0, 1.0, 1.3, 1.0, 1.0, 1.0, 1.0, 1.2, 1.0],
          symbol: 'circle',
          symbolSize: 10,
          lineStyle: { normal: { color: '#ff6b6b' } }
        }]
      },
      option2: {
        title: {
          text: '  20180801',
          left: 'center',
          textStyle: {
            fontSize: 14,
            fontWeight: 'normal'
          },
          padding: 13
        },
        legend: {
          bottom: '2%',
          data: ['']
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c}'
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          data: ['0000', '0200', '0400', '0600', '0800', '1000', '1200', '1400', '1600', '1800', '2000', '2200']
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          scale: true
        },
        grid: {
          top: '13%',
          left: '3%',
          right: '4%',
          bottom: '15%',
          containLabel: true
        },
        series: [{
          name: '',
          type: 'line',
          data: [230, 228, 228, 226, 228, 235, 224, 238, 236, 236, 235, 222],
          symbol: 'circle',
          symbolSize: 10,
          lineStyle: { normal: { color: '#ff6b6b' } }
        }]
      },
      option3: {
        title: {
          text: '  20180801',
          left: 'center',
          textStyle: {
            fontSize: 14,
            fontWeight: 'normal'
          },
          padding: 13
        },
        legend: {
          bottom: '2%',
          data: ['']
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c}'
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          data: ['0000', '0200', '0400', '0600', '0800', '1000', '1200', '1400', '1600', '1800', '2000', '2200']
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          scale: true
        },
        grid: {
          top: '13%',
          left: '3%',
          right: '4%',
          bottom: '15%',
          containLabel: true
        },
        series: [{
          name: '',
          type: 'line',
          data: [32, 35, 33, 33, 34, 36, 40, 42, 42, 41, 42, 38],
          symbol: 'circle',
          symbolSize: 10,
          lineStyle: { normal: { color: '#ff6b6b' } }
        }]
      }
    }
  },
  methods: {
    //
    drawLine(className,data) {
      this.echartsLine = echarts.init(document.getElementsByClassName(className)[0])
      this.echartsLine.setOption(data)
    }
  },
  mounted() {
    this.drawLine('currrent',this.option1)
    this.drawLine('voltage',this.option2)
    this.drawLine('temperature',this.option3)
  }
}
</script>

<style scoped>
.currrent,.voltage,.temperature{
	height:300px;
}
</style>


