<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item>
          <el-input placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!---->
    <el-table :data="taskList" highlight-current-row style="width: 100%;">
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index"></el-table-column>
      <el-table-column label="" prop="taskName"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.taskType | taskMode}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.cyclicMode | taskLoopMode}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.cyclicDate?scope.row.cyclicDate:'-'}}</template>
      </el-table-column>
      <el-table-column label="" prop="startDate">
        <template slot-scope="scope">{{scope.row.startDate | dateFormat('date')}}</template>
      </el-table-column>
      <el-table-column label="" prop="endDate">
        <template slot-scope="scope">{{scope.row.endDate | dateFormat('date')}}</template>
      </el-table-column>
      <el-table-column label="" prop="startTime">
        <template slot-scope="scope">{{scope.row.startTime | dateFormat('time')}}</template>
      </el-table-column>
      <el-table-column label="" prop="endTime">
        <template slot-scope="scope">{{scope.row.endTime | dateFormat('time')}}</template>
      </el-table-column>
      <el-table-column label="" prop="subtaskNames"></el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button type="primary"></el-button>
          <el-button type="danger"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-button type="danger"></el-button>
      <el-button type="warning" @click="sendPauseTask"></el-button>
      <el-button type="primary" @click="sendRestoreTask"></el-button>
      <el-pagination layout="prev, pager, next" :page-size="20" :total="taskList.length" style="float:right;"></el-pagination>
    </el-col>
    <!---->
    <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="taskName">
          <el-input v-model="addForm.taskName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="subtaskIds">
          <el-select v-model="addForm.subtaskIds" :collapse-tags="true" multiple placeholder="">
            <el-option v-for="item in playListOptions" :key="item.subtaskId" :label="item.subtaskName" :value="item.subtaskId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" required>
          <el-radio-group v-model="addForm.type">
            <el-radio class="radio" :label="0"></el-radio>
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="date">
          <el-date-picker v-model="addForm.date" type="daterange" range-separator="" start-placeholder="" end-placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="11">
            <el-form-item prop="startTime">
              <el-time-picker v-model="addForm.startTime" placeholder="" style="width:100%;"></el-time-picker>
            </el-form-item>
          </el-col>
          <el-col :span="2" style="text-align: center;"></el-col>
          <el-col :span="11">
            <el-form-item prop="endTime">
              <el-time-picker v-model="addForm.endTime" placeholder="" style="width:100%;"></el-time-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="addForm.cyclic" active-color="#13ce66" inactive-color="#ff4949" @change="cyclicSwitch"></el-switch>
        </el-form-item>
        <el-form-item label="" prop="cyclicMode">
          <el-radio-group v-model="addForm.cyclicMode" :disabled="!addForm.cyclic">
            <el-radio class="radio-block" :label="1"></el-radio>
            <el-radio class="radio-block" :label="2">
              
              <el-select v-model="addForm.cyclicWeeks" :collapse-tags="true" :disabled="addForm.cyclicMode!==2" multiple placeholder="">
                <el-option v-for="item in weeks" :key="item.value" :label="item.label" :value="item.value"></el-option>
              </el-select>
            </el-radio>
            <el-radio class="radio-block" :label="3">
              
              <el-date-picker type="dates" popper-class="chooseDates" :disabled="addForm.cyclicMode!==3" format="dd" v-model="addForm.cyclicDates" default-value="2018/7/1" placeholder="">
              </el-date-picker>
            </el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit('addForm')"></el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
import { getPlayList, getTaskList, addTimedTask, querySpiritStatus, pauseTask, restoreTask, checkTaskName } from '@/api/api.js'

export default {
  data() {
    var checkName = (rule, value, callback) => {
      if (value) {
        checkTaskName({ taskName: value }).then(res => {
          if (res.result === 'true') {
            callback();
          } else {
            callback(new Error(''));
          }
        })
      }
    };
    var checkMode=(rule,value,callback)=>{
      if(this.addForm.cyclic){
        if(this.addForm.cyclicMode===0){
          callback(new Error(''));
        }else if(this.addForm.cyclicMode===1){
          callback();
        }else if(this.addForm.cyclicMode===2){
          if(this.addForm.cyclicWeeks.length>0){
            callback();
          }else{
            callback(new Error(''));
          }
        }else if(this.addForm.cyclicMode===3){
          if(this.addForm.cyclicDates){
            callback();
          }else{
            callback(new Error(''));
          }
        }
      }else{
        callback();
      }
    }
    return {
      taskList: [],
      addFormVisible: false, //
      //
      addForm: {
        taskName: '',
        type: 0,
        date: '',
        startTime: '',
        endTime: '',
        cyclic: false,
        cyclicMode: 0,
        cyclicDates: '',
        cyclicWeeks: '',
        subtaskIds: ''
      },
      weeks: [{
        value: 1,
        label: ''
      }, {
        value: 2,
        label: ''
      }, {
        value: 3,
        label: ''
      }, {
        value: 4,
        label: ''
      }, {
        value: 5,
        label: ''
      }, {
        value: 6,
        label: ''
      }, {
        value: 7,
        label: ''
      }],
      addFormRules: {
        taskName: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkName, trigger: 'blur' }
        ],
        subtaskIds: [
          { required: true, message: '', trigger: 'change' }
        ],
        date: [
          { required: true, message: '', trigger: 'change' }
        ],
        startTime: [
          { required: true, message: '', trigger: 'change' }
        ],
        endTime: [
          { required: true, message: '', trigger: 'change' }
        ],
        cyclicMode:[
          { validator: checkMode, trigger: 'change' }
        ]
      }
    }
  },
  computed: {
    //
    ifUpdateTaskList:function(){
      return this.$store.state.ifUpdateTimedTaskList;
    },
    //
    playListOptions: function() {
      return this.$store.state.subtasksList;
    },
    taskName: function() {
      return this.addForm.taskName;
    },
    type: function() {
      return this.addForm.type
    },
    mode: function() {
      return this.addForm.cyclicMode
    },
    cyclicDate: function() {
      var cyclicDate = '';
      if (this.addForm.cyclicMode === 0 || this.addForm.cyclicMode === 1) {
        cyclicDate = '';
      } else if (this.addForm.cyclicMode === 2) {
        cyclicDate = this.addForm.cyclicWeeks.join(',');
      } else if (this.addForm.cyclicMode === 3) {
        for (var i = 0; i < this.addForm.cyclicDates.length; i++) {
          if (this.addForm.cyclicDates) {
            this.addForm.cyclicDates[i] = this.addForm.cyclicDates[i].getDate()
          }
        }
        cyclicDate = this.addForm.cyclicDates.join(',');
      }
      return cyclicDate;
    },
    strStartDate: function() {
      var date = this.addForm.date[0];
      return new Date(date).getTime();
    },
    strEndDate: function() {
      var date = this.addForm.date[1];
      return new Date(date).getTime();
    },
    strStartTime: function() {
      var time = this.addForm.startTime;
      return new Date(time).toTimeString().substring(0, 8);
    },
    strEndTime: function() {
      var time = this.addForm.endTime;
      return new Date(time).toTimeString().substring(0, 8);
    },
    subtaskIds: function() {
      return this.addForm.subtaskIds.join(',');
    },
    params: function() {
      var data = {
        taskName: this.taskName,
        type: this.type,
        mode: this.mode,
        cyclicDate: this.cyclicDate,
        strStartTime: this.strStartTime,
        strEndTime: this.strEndTime,
        startTimestamp: this.strStartDate,
        endTimestamp: this.strEndDate,
        subtaskIds: this.subtaskIds
      }
      return data;
    }
  },
  methods: {
    //
    getSubtaskList() {
      const that = this;
      getPlayList().then(res => {
        console.log(res)
        that.$store.commit('updateSubtasksList', res.subtasksList);
      })
    },

    //
    getTaskList() {
      const that = this;
      getTaskList().then(res => {
        console.log(res)
        that.taskList = res.tasksList;
      })
    },

    //
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
    },
    //
    handleAdd: function() {
      this.addFormVisible = true;
    },
    addSubmit(formName) {
      const that = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          addTimedTask(that.params).then(res => {
            console.log(res);
            if (res.result === 'send successful') {
              that.$message.success('!');
              that.addFormVisible = false;
            }
          });
        } else {
          return false;
        }
      })
    },
    cyclicSwitch(val) {
      if (!val) {
        this.addForm.cyclicMode = 0;
      }
    },

    //
    sendPauseTask() {
      pauseTask().then(res => { console.log(res) })
    },

    //
    sendRestoreTask() {
      restoreTask().then(res => { console.log(res) })
    }
  },
  created() {
    this.getTaskList();
    this.getSubtaskList();
  },
  watch:{
    ifUpdateTaskList(val){
      if(val){
        this.getTaskList();
        this.$store.commit('updateTimedTaskList',false);
      }else{
        return;
      }
    }
  }
}

</script>
<style scoped>
.radio-block {
  display: block;
  padding: 10px;
}

</style>



