<template>
  <section>
    <div class="back">
      <el-button type="text" icon="el-icon-arrow-left" @click="$router.go(-1)"></el-button>
    </div>
    <el-tree :data="treeData" :props="treeProps" show-checkbox accordion ref="tree" node-key="key"></el-tree>
    <el-button type="primary" @click="addTask('instant')"></el-button>
    <el-button type="primary" @click="addTask('timer')"></el-button>
    <!--  -->
    <el-dialog title="" :visible.sync="instantTaskVisible" center>
      <el-form :model="instantTaskForm" :rules="instantTaksRules" ref="instantTaskForm">
        <el-form-item label="" prop="state">
          <el-radio v-model="instantTaskForm.state" label="1"></el-radio>
          <el-radio v-model="instantTaskForm.state" label="0"></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="instantTaskForm.delay" :min="100" :max="10000" :step="100"></el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="instantTaskForm.saveTask" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="instantTaskForm.taskName" :disabled="!instantTaskForm.saveTask" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="instantTaskVisible = false"></el-button>
        <el-button type="primary" @click="submitInstantTask('instantTaskForm')"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="timerTaskVisible" center>
      <el-form :model="timerTaskForm" :rules="timerTaskRules" ref="timerTaskForm">
        <el-form-item label="" prop="dayofweek">
          <el-select v-model="timerTaskForm.dayofweek" :collapse-tags="true" multiple placeholder="">
            <el-option v-for="item in weeks" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="startTime">
          <el-time-picker v-model="timerTaskForm.startTime"></el-time-picker>
        </el-form-item>
        <el-form-item label="" prop="endTime">
          <el-time-picker v-model="timerTaskForm.endTime"></el-time-picker>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="timerTaskForm.delay" :min="100" :max="10000" :step="100"></el-input-number>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="timerTaskVisible = false"></el-button>
        <el-button type="primary" @click="submitTimerTask('timerTaskForm')"></el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
import { getGatewayList, checkGatewayTaskName, sendInstantTask, sendTimerTask } from '@/api/api.js';

export default {
  data() {
    return {
      nodeList: [],   //
      instantTaksRules: {
        state: [{ required: true, message: '', trigger: 'change' }]
      },
      timerTaskRules: {
        dayofweek: [{ required: true, message: '', trigger: 'change' }],
        startTime: [{ required: true, message: '', trigger: 'change' }],
        endTime: [{ required: true, message: '', trigger: 'change' }]
      },
      instantTaskForm: {
        delay: 500,
        saveTask: false,
        taskName: '',
        state: ''
      },
      timerTaskForm: {
        delay: 500,
        startTime: '',
        endTime: '',
        dayofweek: ''
      },
      weeks: [{
        label: '',
        value: '1'
      }, {
        label: '',
        value: '2'
      }, {
        label: '',
        value: '3'
      }, {
        label: '',
        value: '4'
      }, {
        label: '',
        value: '5'
      }, {
        label: '',
        value: '6'
      }, {
        label: '',
        value: '7'
      }],
      treeProps: {
        children: 'children',
        label: 'label'
      },
      treeData: [],
      instantTaskVisible: false,
      timerTaskVisible: false
    }
  },
  methods: {
    //
    getNodeList() {
      const that = this;
      getGatewayList().then(res => {
        if (res.result === 'true') {
          var arr = that.createdTreeData(res.intelligentGatewayList, 'rackName', 'groupRelayIp', ['mac', 'rackId']);
          this.treeData = arr;
        }
      })
    },

    /**
     * [createdTreeData description]
     * @param  {Array} list     [description]
     * @param  {String} label    [description]
     * @param  {String} children [description]
     * @param  {Array} params   [description]
     * @return {Array}          [description]
     */
    createdTreeData(list, label, children, params) {
      var arr = [];
      for (var i = 0; i < list.length; i++) {
        arr[i] = {};
        arr[i].label = list[i][label];
        for (var k = 0; k < params.length; k++) {
          arr[i][params[k]] = list[i][params[k]];
        }
        if (children === 'groupRelayIp') {
          for (var n = 0; n < list[i][children].length; n++) {
            list[i][children][n].rackId = list[i].rackId;
          }
          arr[i].children = this.createdTreeData(list[i][children], 'rs485toNetIp', 'relayList', ['rs485toNetIp', 'rackId']);
        } else if (children === 'relayList') {
          for (var m = 0; m < list[i][children].length; m++) {
            list[i][children][m].rackId = list[i].rackId;
          }
          arr[i].children = this.createdTreeData(list[i][children], 'slaveAddr', 'loopList', ['slaveAddr', 'port', 'number', 'rackId'])
        } else if (children === 'loopList') {
          var loopList = [];
          for (var j = 0; j < list[i].number; j++) {
            loopList[j] = {};
            loopList[j].label = '' + (j + 1);
            var data = {
              rackId: list[i].rackId,
              rs485toNetIP: list[i].rs485toNetIp,
              port: list[i].port,
              slaveAddr: list[i].slaveAddr,
              loopNums: j,
              relayId: list[i].id,
              mac: list[i].gatewayMac
            }
            loopList[j].key = JSON.stringify(data);
          }
          arr[i].children = this.createdTreeData(loopList, 'label', '', ['key'])
        }
      }
      return arr;
    },

    //
    addTask(type) {
      // key
      // 0
      // typedata
      var list = this.$refs.tree.getCheckedKeys(true);
      if (list.length === 0) {
        this.$message.warning('');
        return;
      }
      this.instantTaskVisible = type === 'instant' ? true : false;
      this.timerTaskVisible = type === 'timer' ? true : false;

      this.nodeList = list;
    },

    /**
     * 
     * @param  {String} type instant--timer--
     * @param  {Array} arr1  
     * @param  {Array} arr2  485
     * @param  {Array} arr3  
     * @param  {Array} arr4  
     * @return {Object}      
     */
    getTaskParams(type, arr1, arr2, arr3, arr4) {
      var params;
      var rackId = arr1.join(',');
      var taskMsg = {}
      taskMsg.delaymsc = type === 'instant' ? this.instantTaskForm.delay + '' : this.timerTaskForm.delay + '';
      var taskList = [];
      for (var i = 0; i < arr2.length; i++) {
        var data1 = arr2[i].split(',');
        taskList[i] = {};
        taskList[i].rs485toNetIP = data1[0];
        taskList[i].port = data1[1];
        taskList[i].gatewayMac = data1[2];
        taskList[i].tasks = [];
        for (var j = 0; j < arr3.length; j++) {
          var data2 = arr3[j].split(',');
          var loopNums = [];
          for (var k = 0; k < arr4.length; k++) {
            if (arr4[k].rs485toNetIP === data2[0] && arr4[k].slaveAddr == data2[1]) {
              loopNums.push(arr4[k].loopNums)
            }
          }
          if (data1[0] === data2[0]) {
            taskList[i].tasks.push({
              slaveAddr: data2[1],
              loopNums: loopNums.join(','),
              state: type === 'instant' ? this.instantTaskForm.state : '1',
              relayId: data2[2]
            })
          }
        }
      }
      if (type === 'instant') {
        var saveState = this.instantTaskForm.saveTask;
        var taskName = this.instantTaskForm.saveTask ? this.instantTaskForm.taskName : '';
        taskMsg.realTimeTasks = taskList;
        params = { rackId, saveState, taskName, taskMsg }
      } else if (type === 'timer') {
        taskMsg.dayofweek = this.timerTaskForm.dayofweek.join(',');
        taskMsg.startTime = this.timerTaskForm.startTime.toTimeString().substring(0, 8);
        taskMsg.endTime = this.timerTaskForm.endTime.toTimeString().substring(0, 8);
        taskMsg.timlyTasks = taskList;
        params = { rackId, taskMsg };
      }
      return params;
    },

    //,4854
    getSelectedNode() {
      var rackIdArr = []
      var rs485toNetIPArr = []
      var slaveAddrArr = []
      var loopNumsArr = [];
      var list = this.nodeList;
      for (var i = 0; i < list.length; i++) {
        if (list[i]) {
          rackIdArr.push(JSON.parse(list[i]).rackId)
          rs485toNetIPArr.push(JSON.parse(list[i]).rs485toNetIP + ',' + JSON.parse(list[i]).port + ',' + JSON.parse(list[i]).mac)
          slaveAddrArr.push(JSON.parse(list[i]).rs485toNetIP + ',' + JSON.parse(list[i]).slaveAddr + ',' + JSON.parse(list[i]).relayId)
          loopNumsArr.push(JSON.parse(list[i]))
        }
      }
      // 
      rackIdArr = unique1(rackIdArr)
      rs485toNetIPArr = unique1(rs485toNetIPArr)
      slaveAddrArr = unique1(slaveAddrArr)
      var arr = [rackIdArr, rs485toNetIPArr, slaveAddrArr, loopNumsArr]
      return arr;
    },

    //
    submitInstantTask(formName) {
      //
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const that = this;
          var arr = this.getSelectedNode();
          var params = this.getTaskParams('instant', arr[0], arr[1], arr[2], arr[3]);
          // params.saveStatetrue
          // 
          // 
          if (params.saveState) {
            checkGatewayTaskName({ taskName: params.taskName }).then(res => {
              if (res.result === 'true') {
                if (res.exitState) {
                  that.$message.warning('');
                } else {
                  sendInstantTask(params).then(res => {
                    console.log(res)
                    if (res.result === 'send successful') {
                      that.$message.success('');
                      that.instantTaskVisible = false;
                    }
                  })
                }
              }
            })
          } else {
            sendInstantTask(params).then(res => {
              console.log(res)
              if (res.result === 'send successful') {
                that.$message.success('');
                that.instantTaskVisible = false;
              }
            })
          }
        } else {
          return false;
        }
      })
    },

    //
    submitTimerTask(formName) {
      // 
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const that = this;
          var arr = this.getSelectedNode();
          var params = this.getTaskParams('timer', arr[0], arr[1], arr[2], arr[3]);
          sendTimerTask(params).then(res => {
            console.log(res)
            if (res.result === 'send successful') {
              that.$message.success('');
              that.timerTaskVisible = false;
            }
          })
        } else {
          return false;
        }
      })
    }
  },
  created() {
    this.getNodeList();
  }
}

//
function unique1(arr) {
  var hash = [];
  for (var i = 0; i < arr.length; i++) {
    if (hash.indexOf(arr[i]) == -1) {
      hash.push(arr[i]);
    }
  }
  return hash;
}

</script>
<style scoped>
.el-input {
  width: auto;
}

</style>



