import { getFileList, uploadFile } from '@/api/api.js'

/**
 * 
 * @param  {string} mac  mac
 */
class Spirit {
  constructor( mac) {
    this.mac = mac
  }

  /**
   * 
   * @return {object}   status0--1--2--
   */
  getFileLists() {
    var params = {}
    return getFileList(params);
  }

  /**
   * 
   * @param  {number} type 0--1--
   * @param  {string} log  
   * @param  {string} path 
   * @param  {string} md5  md5
   * @param  {number} time ms
   * @return {object}      
   */
  uploadFile(type, path, md5, time) {
    var params = {
      fileType: type,
      md5: md5,
      filePath: path,
      time: parseInt(time)
    }
    return uploadFile(params);
  }
}

export default Spirit



